import React, { memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
  InputNumber,
} from "antd";
import { Tree } from "antd";
import { personal_adjust_cond_config_create } from "../../../api/room";
// import { role_create } from "../../../api/role";
const { Option } = Select;
const { TextArea } = Input;
const Index = (props) => {
  const { onCancel, visible, onOk } = props;
  const [form] = Form.useForm();
  //提交
  const submit = async () => {
    // console.log(form.getFieldsValue());
    try {
      form.validateFields().then(async () => {
        await personal_adjust_cond_config_create({
          ...form.getFieldsValue(),
        });
        form.resetFields();
        message.success("创建成功");
        onOk();
      });
    } catch (e) {
      console.error(e);
    }
  };
  const FormConfig = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18 },
  };

  const on_cancel = () => {
    form.resetFields();
    onCancel();
  };
  return (
    <Modal
      title="添加条件"
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
      okText="确定"
      cancelText="返回"
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item
          label="检测数据类型"
          name="check_data_type"
          rules={[{ required: true, message: "请选择检测数据类型" }]}
        >
          <Select placeholder="请选择检测数据类型">
            <Option value={1}>1-盈亏（不含抽水）百分比</Option>
            <Option value={2}>2-盈亏分数</Option>
            <Option value={3}>3-局数</Option>
            <Option value={4}>4-胜率</Option>
            <Option value={5}>5-比例</Option>
          </Select>
        </Form.Item>

        <Form.Item
          label="依赖事件"
          name="depend_event"
          rules={[{ required: true, message: "请选择依赖事件" }]}
        >
          <Select placeholder="请选择依赖事件">
            <Option value={1}>1-未充值</Option>
            <Option value={2}>2-首充事件</Option>
            <Option value={3}>3-首充金额</Option>
            <Option value={4}>4-最近一次充值金额</Option>
            <Option value={5}>5-N局内2人对局胜率</Option>
          </Select>
        </Form.Item>
        <Form.Item
          label="依赖参数"
          name="depend_param"
          rules={[{ required: true, message: "请输入依赖参数" }]}
        >
          <InputNumber  placeholder="请输入依赖参数" />
        </Form.Item>
        <Form.Item
          label="最小阈值"
          name="check_value_min"
          rules={[{ required: true, message: "请输入最小阈值" }]}
        >
          <InputNumber  placeholder="请输入最小阈值" />
        </Form.Item>
        <Form.Item
          label="最大阈值"
          name="check_value_max"
          rules={[{ required: true, message: "请输入最大阈值" }]}
        >
          <InputNumber  placeholder="请输入最大阈值" />
        </Form.Item>
        <Form.Item
          label="描述"
          name="desc"
          rules={[{ required: true, message: "请输入描述" }]}
        >
          <TextArea rows={4} placeholder="请输入描述" />
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(Index);
