import { Table, Button, Space, message, Popconfirm } from "antd";
import React, {
  useEffect,
  useState,
  forwardRef,
  useImperativeHandle,
} from "react";
import dayjs from "dayjs";
import {
  personal_adjust_cond_config_get,
  personal_adjust_cond_config_delete,
} from "../../../api/room";
const ListTable = (props, ref) => {
  useImperativeHandle(ref, () => ({
    // changeVal 就是暴露给父组件的方法
    getData: (newVal) => {
      personal_adjust_cond_config_get_handle(newVal);
    },
  }));
  const {
    modifypassword_h,
    modifyaccountRole_h,
    block_h,
    pageSize = 10,
    total = 0,
    showTotal = true,
    update_h,
  } = props;
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: pageSize || 10,
    total: total || 0,
    showSizeChanger: true,
  });
  //检测数据类型转换
  const check_data_type_trans_text = (type) => {
    let text;
    switch (type) {
      case 1:
        text = "1-盈亏（不含抽水）百分比";
        break;
      case 2:
        text = "2-盈亏分数";
        break;
      case 3:
        text = "3-局数";
        break;
      case 4:
        text = "4-胜率";
        break;
      case 5:
        text = "5-比例";
        break;
    }
    return text;
  };
  //依赖事件转换
  const depend_event_trans_text = (type) => {
    let text;
    switch (type) {
      case 1:
        text = "1-未充值";
        break;
      case 2:
        text = "2-首充事件";
        break;
      case 3:
        text = "3-首充金额";
        break;
      case 4:
        text = "4-最近一次充值金额";
        break;
      case 5:
        text = "5-N局内2人对局胜率";
        break;
    }
    return text;
  };
  //删除
  const delete_h = async (id) => {
    try {
      await personal_adjust_cond_config_delete(id);
      message.success("删除成功");
      personal_adjust_cond_config_get_handle();
    } catch (error) {
      console.error(error);
    }
  };
  const columns = [
    { title: "条件id", dataIndex: "id", key: "id" },
    {
      title: "检测数据类型",
      dataIndex: "check_data_type",
      render: (_, record) => (
        <span>{check_data_type_trans_text(record.check_data_type)}</span>
      ),
    },
    {
      title: "依赖事件",
      dataIndex: "depend_event",
      render: (_, record) => (
        <span>{depend_event_trans_text(record.depend_event)}</span>
      ),
    },
    {
      title: "依赖参数",
      dataIndex: "depend_param",
    },
    {
      title: "检测阈值最小值",
      dataIndex: "check_value_min",
    },
    {
      title: "检测阈值最大值",
      dataIndex: "check_value_max",
    },
    {
      title: "描述",
      dataIndex: "desc",
    },
    {
      title: "操作",
      dataIndex: "action",
      render: (_, record) => (
        <Space>
          <a onClick={() => update_h(record)}>编辑</a>
          <Popconfirm
            title="确定删除这条数据?"
            onConfirm={() => delete_h(record.id)}
            okText="确定"
            cancelText="取消"
          >
            <a>删除</a>
          </Popconfirm>
        </Space>
      ),
    },
  ];
  //获取列表
  const personal_adjust_cond_config_get_handle = async (params = {}) => {
    try {
      setLoading(true);
      let data = await personal_adjust_cond_config_get({
        page: pagination.current,
        page_size: pagination.pageSize,
        ...params,
      });
      // console.log(data)
      setData(data.data);
        setPagination({
        ...pagination,
        total: data.page.count,
      });
      // console.log(pagination);
      setLoading(false);
    } catch (e) {
      setLoading(false);
      console.error(e);
    }
  };
  //表格改变触发
  const onchange = (pagination) => {
    // console.log(pagination)
    setPagination({ ...pagination });
  };
  useEffect(() => {
    personal_adjust_cond_config_get_handle();
  }, [pagination.current, pagination.pageSize]);
  return (
    <>
      <Table
        bordered
        loading={loading}
        columns={columns}
        dataSource={data}
        rowKey="id"
        pagination={pagination}
        onChange={onchange}
      ></Table>
    </>
  );
};
export default forwardRef(ListTable);
